.search {
	display: flex;
	margin-top:0 auto;
	width: 30%;
	float: right;
	margin-right: 30vh;
	height: 5vh;
	width: 250px;
	border: 3px solid #00B4CC;
	border-radius: 3px;
}

.search-wrapper {
	position: fixed;
	top: 0;
	margin-top: 55px;
	background-color: #A9A9A9;
	padding: 4px;
	width: 100%;
	height: 7vh;
	z-index: 100;
}

@media screen and (max-width: 600px) {
  .search-wrapper {
    padding: 2px;
    margin: 10px;
    margin-left: 0px;
    height: 60px;
  }
}
@media screen and (max-width: 600px) {
  .search {
    padding: 2px;
    margin: 0 auto;
    margin-top: 20px;
    margin-right: 15vh;
  }
}