.category {
  display: flex;
  position: fixed;
  top: 0;
  justify-content: space-around;
  width: 100%;
  background-color: #000000;
  max-height: 10vh;
  z-index: 100;
}
.category div{
  display: inline-block;
  padding: 10px;
  cursor: pointer;
  font-size: 2em;
  color: white;
  margin: 0;
}

.category div:hover{
  color: purple;
}
.category div:active{
  text-decoration: underline;
  color: orange;
}

@media screen and (max-width: 600px) {
   .category div{
      font-size: 0.6em;
  }
}


@media screen and (max-width: 600px) {
  .category {
    padding: 0px;
    margin:0 auto;
  }
}